.increment-decrement-stock{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    border: 0.01rem solid white;
    margin: 0.5rem;
    background-color: white;
    color:var(--maincolor);
    font-size: 2rem;
    height: 2.1rem;

}
.increment-decrement-stock-sign{
    display: flex;
    align-items: center;
    position: absolute;
    border: 0.01rem solid white;
    margin: 0.5rem;
    background-color: white;
    color:var(--maincolor);
    font-size: 1.2rem;

}

.right{
   right:0;

}
.right:hover{
   color:green; 
   cursor: pointer;  
 }
.left{
  left: 0;
}
.left:hover{
    color: var(--secondaryColor);
    cursor: pointer;  
     
  }
.increment-decrement-stock-n{
    display: flex;
    align-items: center;
    justify-content: center;
    
    border: 0.01rem solid white;
    margin: 0.5rem;
    background-color: white;
    color:var(--maincolor);
    font-size: 1.5rem;
    user-select: none;

}
.disable-sign-l{
    left: 0;
    color: #cccccc;
    cursor: default;
}
.disable-sign-r{
    right:  0;
    color: #cccccc;
    cursor: default;
}
.increment-decrement-disponible{
    font-size: 1.1rem;
    text-align: center;
    font-style: italic;
    color: #444444;
}